
'use client';

import { useNftStore } from '@/hooks/use-nft-store';
import { useWallet } from '@/contexts/wallet-context';
import { useEffect, useMemo, useState } from 'react';
import type { Nft } from '@/lib/types';
import Image from 'next/image';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { useToast } from '@/hooks/use-toast';
import { Skeleton } from '@/components/ui/skeleton';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { AlertTriangle, ArrowRight } from 'lucide-react';
import { Separator } from '@/components/ui/separator';
import { format } from 'date-fns';
import { useIsClient } from '@/hooks/use-is-client';

export default function NftPage({ params }: { params: { hash: string } }) {
  const { getNftByHash, transferNft, isLoaded } = useNftStore();
  const { walletAddress, isConnected } = useWallet();
  const [nft, setNft] = useState<Nft | null | undefined>(undefined);
  const [newOwnerAddress, setNewOwnerAddress] = useState('');
  const { toast } = useToast();
  const isClient = useIsClient();
  
  useEffect(() => {
    if (isLoaded && isClient) {
      const foundNft = getNftByHash(params.hash);
      setNft(foundNft);
    }
  }, [params.hash, isLoaded, getNftByHash, isClient]);

  const formattedDates = useMemo(() => {
    if (!isClient || !nft || nft.owners.length <= 1) return [];
    
    const reversedOwners = [...nft.owners].reverse();
    
    return reversedOwners.slice(1).map(owner => 
        format(new Date(owner.timestamp), "PPp")
    );
  }, [nft, isClient]);


  const handleTransfer = () => {
    if (!newOwnerAddress.trim() || !nft) {
      toast({
        title: 'Error',
        description: 'Please enter a valid wallet address.',
        variant: 'destructive',
      });
      return;
    }
    const previousOwner = nft.owners[nft.owners.length - 1].address;
    const success = transferNft(params.hash, newOwnerAddress);
    if (success) {
      toast({
        title: 'Transfer Successful!',
        description: (
          <div>
            <p>Ownership has been transferred.</p>
            <div className="mt-2 flex items-center text-sm">
              <span className="font-mono bg-muted text-muted-foreground p-1 rounded-md">{`${previousOwner.slice(0, 6)}...`}</span>
              <ArrowRight className="mx-2 h-4 w-4 text-primary" />
              <span className="font-mono bg-muted text-muted-foreground p-1 rounded-md">{`${newOwnerAddress.slice(0, 6)}...`}</span>
            </div>
          </div>
        )
      });
      const updatedNft = getNftByHash(params.hash);
      setNft(updatedNft);
      setNewOwnerAddress('');
    } else {
      toast({
        title: 'Transfer Failed',
        description: 'An unexpected error occurred.',
        variant: 'destructive',
      });
    }
  };

  if (!isClient || !isLoaded || nft === undefined) {
    return (
      <div className="container mx-auto px-4 py-8 max-w-4xl">
        <Card>
          <CardHeader>
             <Skeleton className="aspect-[16/9] w-full mb-6 rounded-md"/>
             <Skeleton className="h-10 w-3/4" />
             <Skeleton className="h-6 w-full mt-2" />
          </CardHeader>
           <CardContent>
              <Skeleton className="h-40 w-full mt-4" />
           </CardContent>
        </Card>
      </div>
    );
  }

  if (nft === null) {
    return (
      <div className="container mx-auto px-4 py-8 flex justify-center items-center h-[50vh]">
        <Alert variant="destructive" className="max-w-lg">
          <AlertTriangle className="h-4 w-4" />
          <AlertTitle>NFT Not Found</AlertTitle>
          <AlertDescription>
            The NFT with hash "{params.hash}" could not be found. Please check the hash and try again.
          </AlertDescription>
        </Alert>
      </div>
    );
  }

  const currentOwner = nft.owners[nft.owners.length - 1];
  const isOwner = isConnected && walletAddress === currentOwner.address;
  const ownershipHistory = [...nft.owners].reverse();

  return (
    <div className="container mx-auto px-4 py-8 max-w-4xl">
      <Card>
        <CardHeader>
          <div className="relative aspect-[16/9] w-full mb-6">
            <Image
              src={nft.imageUrl}
              alt={nft.title}
              fill
              className="object-cover rounded-md"
              data-ai-hint={nft.imageHint}
            />
          </div>
          <CardTitle className="font-headline text-4xl">{nft.title}</CardTitle>
          <CardDescription>{nft.description}</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="prose prose-invert max-w-none text-foreground/90 whitespace-pre-wrap font-body text-base">
            {nft.content}
          </div>
          <div className="mt-8 text-sm text-muted-foreground">
            <p>
              <strong>Hash:</strong> {nft.hash}
            </p>
          </div>
        </CardContent>
        <CardFooter className="flex-col items-start gap-6">
          <Separator />
          <div>
            <h3 className="font-headline text-xl mb-4">Ownership History</h3>
            <div className="space-y-4">
              {ownershipHistory.map((owner, index) => (
                <div key={index} className="flex items-center gap-4 text-sm">
                  <div className="font-mono bg-secondary text-secondary-foreground p-2 rounded-md truncate">
                    {owner.address}
                  </div>
                  {index === 0 && (
                    <span className="text-xs font-semibold text-primary py-1 px-2 rounded-full bg-primary/10">CURRENT OWNER</span>
                  )}
                  {index > 0 && (
                     <span className="text-xs text-muted-foreground">
                       {formattedDates[index -1]}
                     </span>
                  )}
                </div>
              ))}
            </div>
          </div>
          
          {isOwner && (
            <Card className="mt-8 bg-secondary w-full">
              <CardHeader>
                <CardTitle className="font-headline text-xl">Transfer Ownership</CardTitle>
                <CardDescription>
                  Enter the wallet address of the new owner to transfer this NFT. This action is irreversible.
                </CardDescription>
              </CardHeader>
              <CardContent className="flex gap-2">
                <Input
                  placeholder="Recipient's wallet address (e.g., 0x...)"
                  value={newOwnerAddress}
                  onChange={(e) => setNewOwnerAddress(e.target.value)}
                />
                <Button onClick={handleTransfer}>Transfer</Button>
              </CardContent>
            </Card>
          )}
        </CardFooter>
      </Card>
    </div>
  );
}
